import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  setSelectedDate,
  getGamesInfo,
  onFavouriteChange,
  getBoxscoreInfo
} from '../actions';
import moment from 'moment';
import GamesList from '../components/GameListView/GamesList';
import DateSelect from '../components/GameListView/DateSelect';
import FavouriteSelect from '../components/GameListView/FavouriteSelect';

class ListView extends Component {
  /**
   * Return new list of games ordered with favourite team at the beginning
   * @param {Array} games list of games for current day
   * @param {string} favourite favourite team
   */
  orderGames = (games, favourite) => {
    favourite = favourite.toLowerCase().trim();
    let sorted = [];
    for (let i = 0; i < games.length; i++) {
      if (
        games[i].home_team_name.toLowerCase().trim() === favourite ||
        games[i].away_team_name.toLowerCase().trim() === favourite
      ) {
        sorted.unshift(games[i]);
      } else {
        sorted.push(games[i]);
      }
    }
    return sorted;
  };

  /**
   * Show game details for the given game
   * If the game was cancelled or postponed, show an alert
   * @param {Object} game game which user selects
   */
  onGameSelect = game => {
    const status = game.status.status;
    if (status === 'Cancelled' || status === 'Postponed' || status === 'Preview') {
      alert('The game has been cancelled, postponed or has not been played. There are no details to show.');
    } else {
      this.props.getBoxscoreInfo(game.game_data_directory);
      this.props.history.push('/details');
    }
  };

  /**
   * Retrieve new games for new date, if date is valid
   * @param {Object} event date input event
   */
  updateGamesInfo = event => {
    let date = moment(event.target.value);
    if (date.isValid()) {
      date = date.format('YYYY-MM-DD');
      this.props.getGamesInfo(date);
    } else {
      alert('Invalid date. Please enter a valid date.');
    }
  };

  /**
   * For next/previous day toggle buttons
   * Updates current date and then updates games list
   * @param {Number} num number of days to add (negative to remove days)
   */
  toggleDate = num => {
    let date = moment(this.props.session.date)
      .add(num, 'days')
      .format('YYYY-MM-DD');
    this.props.setSelectedDate(date);
    this.props.getGamesInfo(date);
  };

  onFavouriteChange = event => {
    this.props.onFavouriteChange(event.target.value);
  };

  updateDate = event => {
    this.props.setSelectedDate(event.target.value);
  };

  isWinning = (teamScore, opponentScore) => {
    if (parseInt(teamScore) > parseInt(opponentScore)) {
      return true;
    }
    return false;
  };

  render() {
    const { games, favourite, date } = this.props.session;

    return (
      <div className="row">
        <div className="col-md-offset-4 col-md-4">
          <DateSelect
            date={date}
            updateDate={this.updateDate}
            decreaseDate={() => this.toggleDate(-1)}
            increaseDate={() => this.toggleDate(1)}
            updateGamesInfo={this.updateGamesInfo}
          />

          <div style={{ fontSize: 11, textAlign: 'center' }}>
            *Press Enter to update (YYYY-MM-DD)
          </div>

          <FavouriteSelect
            favourite={favourite}
            onFavouriteChange={this.onFavouriteChange}
          />

          <GamesList
            games={this.orderGames(games, favourite)}
            isWinning={this.isWinning}
            onGameSelect={this.onGameSelect}
          />
        </div>
      </div>
    );
  }
}

function mapStateToProps({ session }) {
  return {
    session
  };
}

export default connect(mapStateToProps, {
  setSelectedDate,
  getGamesInfo,
  onFavouriteChange,
  getBoxscoreInfo
})(ListView);



