import React from 'react';

/**
 * Render date select component
 * @param {string} date format: YYYY-MM-DD
 * @param {Function} updateData change current date
 * @param {Function} decreaseDate decrease current date by 1 day
 * @param {Function} increaseDate increase current date by 1 day
 * @param {Function} updateGamesInfo update games info using new date
 */
const DateSelect = ({ date, updateDate, decreaseDate, increaseDate, updateGamesInfo }) => {
  return (
    <div className="date-select">
      <button className="btn btn-default" onClick={decreaseDate}>
        <span className="glyphicon glyphicon-chevron-left" />
      </button>

      <input
        className="date-input"
        value={date}
        onChange={updateDate}
        onKeyPress={e => (e.key === 'Enter' ? updateGamesInfo(e) : null)}
      />

      <button className="btn btn-default" onClick={increaseDate}>
        <span className="glyphicon glyphicon-chevron-right" />
      </button>
    </div>
  );
};

export default DateSelect;



