import {
  SET_GAMES_INFO,
  SET_SELECTED_DATE,
  ON_FAVOURITE_CHANGE,
  SET_BOXSCORE_INFO,
} from '../actions';

const INIT_STATE = {
  games: [],
  date: '2014-03-29',
  favourite: 'Blue Jays',
  boxscore: null,
};

export default function(state = INIT_STATE, action) {
  switch (action.type) {
    case SET_GAMES_INFO:
      return {
        ...state,
        games: action.payload
      };
    case SET_SELECTED_DATE:
      return {
        ...state,
        date: action.payload
      };
    case ON_FAVOURITE_CHANGE:
      return {
        ...state,
        favourite: action.payload
      };
    case SET_BOXSCORE_INFO:
      return {
        ...state,
        boxscore: action.payload
      };
    default:
      return state;
  }
}



