import React from 'react';

/**
 * Render return to games component
 * @param {Object} history router history 
 */
const ReturnToGames = ({ history }) => {
  return (
    <div className="details-return">
      <a className="btn btn-default" onClick={() => history.goBack()}>
        <span
          style={{ marginRight: 4 }}
          className="glyphicon glyphicon-chevron-left"
        />
        Return
      </a>
    </div>
  );
};

export default ReturnToGames;



