import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import ReduxThunk from 'redux-thunk';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import reducers from './reducers';
import { onFavouriteChange } from './actions';
import App from './containers/App';
import GameListView from './containers/GameListView';
import GameDetailView from './containers/GameDetailView';
import ScrollToTop from './components/ScrollToTop';

import 'normalize.css';
import './index.css';

const createStoreWithMiddleware = applyMiddleware(ReduxThunk)(createStore);
const store = createStoreWithMiddleware(reducers);

// Load favourite team from localstorage
const favourite = localStorage.getItem('favourite');
if (favourite) {
  store.dispatch(onFavouriteChange(favourite));
}

ReactDOM.render(
  <Provider store={store}>
    <BrowserRouter>
      <Switch>
        <App>
          <ScrollToTop>
            <Route path="/details" component={GameDetailView} />
          </ScrollToTop>
          <Route exact path="/" component={GameListView} />
        </App>
      </Switch>
    </BrowserRouter>
  </Provider>,
  document.querySelector('#root')
);



