import React from 'react';

/**
 * Render pitchers table component
 * @param {Object} boxscore game information
 * @param {string} selectedTeam currently selected team
 */
const PitcherTable = ({ boxscore, selectedTeam }) => {
  if (!boxscore) return null;

  // Get the pitchers for the selected team
  const pitchers = boxscore.pitching.filter(
    item => item.team_flag === selectedTeam
  )[0].pitcher;

  return (
    <table className="table table-bordered table-details">
      <thead>
        <tr>
          <th>Name</th>
          <th>IP</th>
          <th>H</th>
          <th>R</th>
          <th>ER</th>
          <th>SO</th>
          <th>BB</th>
          <th>HR</th>
        </tr>
      </thead>
      <tbody>
        {pitchers.map(pitcher => {
          return (
            <tr key={pitcher.id}>
              <td>{pitcher.name_display_first_last}</td>
              <td>{pitcher.s_ip}</td>
              <td>{pitcher.s_h}</td>
              <td>{pitcher.s_r}</td>
              <td>{pitcher.s_er}</td>
              <td>{pitcher.s_so}</td>
              <td>{pitcher.s_bb}</td>
              <td>{pitcher.hr}</td>
            </tr>
          );
        })}
      </tbody>
    </table>
  );
};

export default PitcherTable;



