import React from 'react';

/**
 * Render team select component
 * @param {Object} boxscore game information
 * @param {string} selectedTeam currently selected team
 * @param {Function} changeSelectedTeam change selected team
 */
const TeamSelect = ({ boxscore, selectedTeam, changeSelectedTeam }) => {
  if (!boxscore) return null;

  return (
    <div className="team-select">
      <a className={selectedTeam === 'home' ? 'underline bold' : null}
        onClick={() => changeSelectedTeam('home')} >
        {boxscore.home_fname}
      </a> 
      <a className={selectedTeam === 'away' ? 'underline bold' : null}
        onClick={() => changeSelectedTeam('away')} >
        {boxscore.away_fname}
      </a>
    </div>
  );
}

export default TeamSelect;


