import React from 'react';

/**
 * Render list of games table
 * @param {Array} games list of games for current day
 * @param {Function} isWinning check to see if team won
 * @param {Function} onGameSelect user selects a game
 */
const GamesList = ({ games, isWinning, onGameSelect }) => {
  if (!games.length) {
    return <div className="no-games">No games today!</div>;
  }

  return (
    <table className="table table-bordered games-list">
      <thead>
        <tr>
          <th>Games list!</th>
        </tr>
      </thead>
      <tbody>
        {games.map(game => {
          const home = game.linescore ? game.linescore.r.home : null;
          const away = game.linescore ? game.linescore.r.away : null;
          return (
            <tr key={game.id} onClick={() => onGameSelect(game)}>
              <td>
                <span className="pull-left">
                  <span className={isWinning(home, away) ? 'bold' : null}>
                    {game.home_team_name}
                  </span>
                  <span className={isWinning(away, home) ? 'bold' : null}>
                    {game.away_team_name}
                  </span>
                  <span>{game.status.status}</span>
                </span>
                <span className="pull-right">
                  <span>{home}</span>
                  <span>{away}</span>
                </span>
              </td>
            </tr>
          );
        })}
      </tbody>
    </table>
  );
};

export default GamesList;



