import axios from 'axios';

export const SET_GAMES_INFO = 'SET_GAMES_INFO';
export const SET_SELECTED_DATE = 'SET_SELECTED_DATE';
export const ON_FAVOURITE_CHANGE = 'ON_FAVOURITE_CHANGE';
export const SET_BOXSCORE_INFO = 'SET_BOXSCORE_INFO';

const BASE_URL = 'http://gd2.mlb.com';

/**
 * Retrieves a list of JSON objects representing games and other information from MLB API
 * For more information, look at: http://gd2.mlb.com
 * @param {string} date format: YYYY-MM-DD
 */
export function getGamesInfo(date) {
  const splitDate = date.split('-');
  const url = `${BASE_URL}/components/game/mlb/year_${splitDate[0]}/month_${splitDate[1]}/day_${splitDate[2]}/master_scoreboard.json`;
  return dispatch => {
    axios.get(url).then(res => {
      // Ensure object isn't undefined
      res = res.data.data.games.game || [];
      // Single game returns object; ensure we always have an array
      const games = Array.isArray(res) ? res : [res];
      dispatch(setGamesInfo(games));
    }).catch(err => {
      alert(`An error has occured. Please try again or select another date. \n${err}`);
    });
  };
}

/**
 * Retrieves specific game information using game_data_directory retrieved from getGamesInfo 
 * @param {string} gameDataDirectory path described by game_data_directory
 */
export function getBoxscoreInfo(gameDataDirectory) {
  const url = `${BASE_URL}${gameDataDirectory}/boxscore.json`;
  return dispatch => {
    axios.get(url).then(res => {
      res = res.data.data.boxscore;
      dispatch(setBoxscoreInfo(res));
    }).catch(err => {
      alert(`An error has occured. Please try again or select another game. \n${err}`);
    });
  };
}

export function setGamesInfo(gamesInfo) {
  return {
    payload: gamesInfo,
    type: SET_GAMES_INFO
  };
}

export function setSelectedDate(date) {
  return {
    payload: date,
    type: SET_SELECTED_DATE
  };
}

export function setBoxscoreInfo(info) {
  return {
    payload: info,
    type: SET_BOXSCORE_INFO
  };
}

export function onFavouriteChange(value) {
  localStorage.setItem('favourite', value);
  return {
    payload: value,
    type: ON_FAVOURITE_CHANGE
  };
}






