import React from 'react';

/**
 * Render select favourite component
 * @param {string} favourite favourite team
 * @param {Function} onFavouriteChange update favourite team
 */
const FavouriteSelect = ({ favourite, onFavouriteChange }) => {
  return (
    <div className="favourite-select">
      <label>Favourite:</label>
      <input
        className="form-control"
        value={favourite}
        onChange={onFavouriteChange}
      />
    </div>
  );
};

export default FavouriteSelect;



