import React from 'react';

/**
 * Render batters table component
 * @param {Object} boxscore game information
 * @param {string} selectedTeam currently selected team
 */
const BatterTable = ({ boxscore, selectedTeam }) => {
  if (!boxscore) return null;

  // Get the batters for the selected team
  const batters = boxscore.batting.filter(
    item => item.team_flag === selectedTeam
  )[0].batter;

  return (
    <table className="table table-bordered table-details">
      <thead>
        <tr>
          <th>Name</th>
          <th>AB</th>
          <th>R</th>
          <th>H</th>
          <th>RBI</th>
          <th>BB</th>
          <th>SO</th>
          <th>AVG</th>
        </tr>
      </thead>
      <tbody>
        {batters.map(batter => {
          return (
            <tr key={batter.id}>
              <td>{batter.name_display_first_last}</td>
              <td>{batter.ab}</td>
              <td>{batter.r}</td>
              <td>{batter.h}</td>
              <td>{batter.rbi}</td>
              <td>{batter.bb}</td>
              <td>{batter.so}</td>
              <td>{batter.avg}</td>
            </tr>
          );
        })}
      </tbody>
    </table>
  );
};

export default BatterTable;



