import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getGamesInfo } from '../actions';

/**
 * Primary container for the application
 */
class App extends Component {
  componentDidMount() {
    const { date } = this.props.session;
    this.props.getGamesInfo(date);
  }

  render() {
    return (
      <div className="container">
        {this.props.children}
      </div>
    );
  }
}

function mapStateToProps({ session }) {
  return {
    session
  };
}

export default connect(mapStateToProps, { getGamesInfo })(App);



