import React, { Component } from 'react';
import { connect } from 'react-redux';
import Linescore from '../components/GameDetailView/Linescore';
import ReturnToGames from '../components/GameDetailView/ReturnToGames';
import TeamSelect from '../components/GameDetailView/TeamSelect';
import BatterTable from '../components/GameDetailView/BatterTable';
import PitcherTable from '../components/GameDetailView/PitcherTable';

/**
 * Main container for linescore, batters and pitchers details
 */
class DetailView extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedTeam: 'home'
    };
  }

  changeSelectedTeam = team => {
    this.setState({ selectedTeam: team });
  };

  render() {
    const { boxscore } = this.props.session;

    return (
      <div className="row">
        <div className="col-md-offset-2 col-md-8">
          <ReturnToGames history={this.props.history} />

          <Linescore boxscore={boxscore} />

          <TeamSelect
            boxscore={boxscore}
            selectedTeam={this.state.selectedTeam}
            changeSelectedTeam={this.changeSelectedTeam}
          />

          <h1>Batters</h1>
          <BatterTable
            boxscore={boxscore}
            selectedTeam={this.state.selectedTeam}
          />

          <h1>Pitchers</h1>
          <PitcherTable
            boxscore={boxscore}
            selectedTeam={this.state.selectedTeam}
          />
        </div>
      </div>
    );
  }
}

function mapStatetoProps({ session }) {
  return {
    session
  };
}

export default connect(mapStatetoProps, null)(DetailView);



