import React from 'react';

/**
 * Render linescore component
 * @param {Object} boxscore game information
 */
const Linescore = ({ boxscore }) => {
  if (!boxscore) return null;

  const { linescore, home_fname, away_fname } = boxscore;
  const {
    home_team_runs,
    home_team_hits,
    home_team_errors,
    away_team_runs,
    away_team_hits,
    away_team_errors
  } = linescore;

  return (
    <table className="table table-bordered linescore-table">
      <thead>
        <tr>
          <th />
          {linescore.inning_line_score.map(inning => {
            return <th key={inning.inning}>{inning.inning}</th>;
          })}
          <th>R</th>
          <th>H</th>
          <th>E</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{home_fname}</td>
          {linescore.inning_line_score.map(inning => {
            return <td key={inning.inning}>{inning.home}</td>;
          })}
          <td>{home_team_runs}</td>
          <td>{home_team_hits}</td>
          <td>{home_team_errors}</td>
        </tr>
        <tr>
          <td>{away_fname}</td>
          {linescore.inning_line_score.map(inning => {
            return <td key={inning.inning}>{inning.away}</td>;
          })}
          <td>{away_team_runs}</td>
          <td>{away_team_hits}</td>
          <td>{away_team_errors}</td>
        </tr>
      </tbody>
    </table>
  );
};

export default Linescore;



